import React from 'react';
import {
  withStyles,
  Paper,
  Box
} from '@material-ui/core';

import ReactStickies from 'react-stickies'; //ES6
import { v4 as uuidv4 } from 'uuid';
import StarProgressBar from '../../components/StarProgressBar';
import NextButton from '../../components/NextButton';

const styles = {
  content: {
    width: '60vw',
    height: '80vh',
    margin: 'auto',
    borderSpacing: 'auto',
    padding: '4vw'
  }
}

class DragAndDropPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {}
    this.state.notes = this.initializeNotes();
    this.onChange = this.onChange.bind(this);
    // this.initializeNotes = this.initializeNotes.bind(this);
  }

  onChange(notes) {
    this.setState({ // Update the notes state
      notes
    })
  }

  initializeNotes() {
    const words = ["Sports", "Music", "Crafting", "Puzzles", "Reading", "Writing", "Gardening", "Cooking"];
    return words.map(w => {
      const id = uuidv4();
      const x_rand = Math.random() * 8;
      return {
        "id": id,
        "text": w,
        "grid": {
          "i": id,
          "w": 1,
          "h": 1,
          "x": x_rand,
          "y": 0,
          "isDraggable": true
        },
        "contentEditable": false,
      }

    });
  }
  render() {
    const { classes } = this.props;

    return (
      <React.Fragment>
        <Paper className={classes.content} elevation={3}>
          <Box className={classes.content}>
            <ReactStickies
              title={false}
              footer={false}
              notes={this.state.notes}
              onChange={this.onChange}
              grid={{ isResizable: false, w: 1, h: 1 }}
            />
          </Box>

          <NextButton link="/career/dragdrop" />
        </Paper>
        <StarProgressBar currStars={1} maxStars={5} />


      </React.Fragment>
    )
  }
}

export default withStyles(styles)(DragAndDropPage);
